.login {
  width: 100%;
  max-width: 330px;
  margin: 50px auto;
  color: #272727;
  border-radius: 8px;
  box-sizing: border-box;
}

.ltit{
font-size: 20px;
margin-bottom: 24px;
}

.form {
  width: 100%;
}

.inputWrap{
  margin-bottom: 10px;
}

input{
  height: 54px;
  width: 100%;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 0 12px;
  line-height: 44px;
  transition: padding 0.2s ease;
  box-sizing: border-box;
}

/* 포커스 효과 */
.input:focus {
  border-color: #272727;
  outline: none;
}



/* placeholder 색상 */
.input::placeholder {
  color:#aaa;
}


.find {
  text-align: center;
  margin: 9px -90px 27px 80px;
}


.find span{padding: 0 5px;}

.find button {
 background: none;
 border: none; 
 color: #666;
 cursor: pointer;  
}

.actions {
display: flex;
flex-direction: column; /* 세로 정렬 */
gap: 10px;              /* 버튼 간격 */
}

.loginBtn{
  height: 54px;
  width: 100%;
  padding: 0 11px 1px 5px;
  font-size: 14px;
  background-color: #5f0080;
  border-radius: 7px;
  color: #fff;
  border:none;
}

.joinBtn{
  height: 54px;
  width: 100%;
  padding: 0 11px 1px 5px;
  font-size: 14px;
  background-color: #fff;
  border-radius: 7px;
  color: #5f0080;
  border: 1px solid #5f0080;
}


@media (max-width: 480px) {
  .login h4 {
    font-size: 22px;
  }

  .login button {
    height: 44px;
    font-size: 14px;
  }
}

