/* 공통 */
.header {
  border-bottom: 1px solid #eee;
}

/* ───────── 상단 유틸 헤더 ───────── */
.top {
  border-top: 2px solid #353535;
}

.topInner {
  max-width: 1200px;
  margin: 0 auto;
  height: 32px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 40px;
}

.topMenu {
  display: flex;
  gap: 11px;
}

.topMenu button {
  background: none;
  border: none;
  font-size: 12px;
  color: #666;
  cursor: pointer;
}

.topMenu button:hover {
  text-decoration: underline;
}

/* ───────── 메인 헤더 ───────── */
.main {
  background: #fff;
}

.mainInner {
  max-width: 1200px;
  margin: 0 auto;
  height: 100px;
  padding: 0 40px;
  display: flex;
  align-items: center;
}

/* 왼쪽 */
.left {
  display: flex;
  align-items: center;
  gap: 18px;
  min-width: 260px;
}

.logo img {
  height: 55px;
  display: block;
}


/* 탭 */
.tabs {
  display: flex;
  align-items: center;
  gap: 8px;
}

.tabs span{
  font-size: 12px;
  color: #9e9e9e;
}

.tabs button {
  background: none;
  border: none;
  font-size: 18px;
  color: #9e9e9e;
  cursor: pointer;
}

.tabs .active {
  color: #5f0080;
  font-weight: 600;
}

.header {
  max-width: 1050px;
  margin: 0 auto;
  padding: 0 8px;
}

.mainInner {
  display: flex;
  align-items: center;
  max-width: 1050px;
  margin: 0 auto;
  padding: 0 8px;
  gap: 70px;
}

/* 검색창 */
.search {
  flex: 1; /* ⭐ 핵심 */
  max-width: 380px;
  display: flex;
  align-items: center;
  border: 1px solid #5f0080;
  border-radius: 4px;
  padding: 0 12px;
  height: 48px;
}

/* input */
.search input {
  flex: 1;
  border: none;
  outline: none;
  height: 100%;            /* 부모 높이에 맞춤 */
  font-size: 15px;
  padding: 0 8px;          /* 내부 여백 */
}


.search input:focus {
  outline: none;
}

.searchBtn {
  width: 44px;
  background: none;
  border: none;
  cursor: pointer;
}

.search button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 18px;
  color: #666;
}

/* 오른쪽 아이콘 그룹 */
.icons {
  display: flex;
  align-items: center;
  gap: 20px; /* 아이콘 간격 */
}

.icons button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 20px;
  color: #666;
  display: flex;
  align-items: center;
}

/* 아이콘 호버 효과 */
.icons button:hover {
  color: #5f0080;
}


/* 태블릿 */
@media (max-width: 1024px) {
  .mainInner {
    gap: 50px;
  }
}

@media (max-width: 768px) {
  .search {
    max-width: 100%;
  }
  .mainInner {
    gap: 20px;
  }
}

